body {
  /* background-color: #f1eee6; */
  /* overflow: hidden; */
  /* background-image: url(./assets/AU-FG-Texture1-8K.jpg); */
  background-size: cover;
  background-blend-mode:color-dodge;
}

html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  transition: .5s;
  /* background-color: #f1eee6; */
}



.thlContainer {
  cursor: pointer;
}

.xContainer {
  cursor: pointer;
}

.customCursor {
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 9999;
}

.sidebar {
  height: 100%;
  width: 100%;
  left: -100%;
  top: 0%;
  position: fixed;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: flex-start;
  background-color: #353935;
  /* opacity: 100%; */
  z-index: 10;
  /* transition: left 0.3s ease-in-out; */
  opacity: 0;
  transition: opacity .2s linear;
}

.sidebar.open {
  left: 0;
  opacity: 100;
  /* transition: opacity .2s linear; */
}

.link {
  text-decoration: none;
  cursor: pointer;
  color: #f1eee6;
  font-size: 25px;
  font-weight: 100; 
  margin-left: 5%; 
  letter-spacing: 5px; 
  float: left;
  transition: .5s;
}

.link:hover {
  text-decoration: underline;
}

.link2 {
  text-decoration: none;
}

.link2:hover {
  text-decoration: underline;
}

.option {
  text-decoration: none;
  cursor: pointer;
  text-decoration: none;
  cursor: pointer;
  color: #f1eee6;
  font-size: 40px;
}

.option:hover {
  text-decoration: underline;
}

.optionContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
}

.grain {
  width: 2px;
  height: 2px;
  border-radius: 1px;
  background-color: black; 
  pointer-events: none;
}

input {
  background-color: transparent;
  border-radius: 5px;
  height: 40px;
  transition: .5s;
  width: 350px;
}

button {
  background-color: transparent;
  border-radius: 5px;
  height: 50px;
  transition: .5s;
  width: 150px;
  cursor: pointer;
}



